.App {
  text-align: center;
}
body{
  background-color: #b4dde9;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.container{
  background-color: rgb(7,2,71);
  justify-content: center;
  width: 50%;
  margin-left: 25%;
  margin-top: 5%;
  text-align: center;
  padding: 20px;
}
input[type = text],select{
  width: 80%;
  padding: 12px 20px;
  margin: 30px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;

}
button{
  width: 15%;
  background-color: rgb(39, 130, 39);
  color: black;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;

}
button:hover{
  background-color: rgb(84, 234, 84);

}
.header{
  width: 100%;
  padding: 12px 20px;
  color: white;
  font-size: 50px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

}
.List-heading{
  color: rgb(15,14,14);
  font-size: 50px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-align: left;
  background-color: cadetblue;
  width: 60%;
}
.listData{
  color: black;
  font-size: 20px;
  background-color: rgb(164,203,237);
  text-align: left;
  padding-left: 30px;
  width: 53%;
  padding-bottom: 2%;
  padding-top: 2%;
  border-radius: 10px;
}
.btn-position{
  position: relative;
  left: 15%;
  top: -54px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
